<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <div slot="header" class="clearfix">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-input placeholder="" v-model="queryInfo.keywords" clearable @clear="groupList">
              <el-button slot="append" icon="el-icon-search" @click="groupList"></el-button>
            </el-input>
          </el-col>
        </el-row>
      </div>
      <!--  -->
      <el-row>
        <el-button type="primary" icon="el-icon-plus" @click="addDialog" size="mini"></el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteClick()"></el-button>
      </el-row>
      <!--  -->
      <el-table :data="groupListData" border style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column prop="name" label="" width="180"></el-table-column>
        <el-table-column prop="user" label="" width="180"></el-table-column>
        <el-table-column prop="instruction" label=""></el-table-column>
        <el-table-column prop="create_time" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip :enterable="false" effect="dark" content="" placement="top" v-if="scope.row.status === '1'">
              <el-button type="danger" icon="el-icon-remove-outline" size="mini" @click="statusEdit(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" effect="dark" content="" placement="top" v-else>
              <el-button type="success" icon="el-icon-circle-plus-outline" size="mini" @click="statusEdit(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" effect="dark" content="" placement="top">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="editDialog(scope.row.id)"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" effect="dark" content="" placement="top">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteClick(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.current_page"
        :page-sizes="[25, 30, 40, 50]"
        :page-size="queryInfo.per_page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInfo.total"
      ></el-pagination>

      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" @close="addFormClose" width="50%">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
          <el-form-item label="" prop="name"><el-input placeholder="" v-model="addForm.name"></el-input></el-form-item>
          <el-form-item label="" prop="instruction"><el-input placeholder="" type="textarea" v-model="addForm.instruction"></el-input></el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="addForm.status" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="rules"><el-tree :data="addForm.children" ref="addTree" show-checkbox node-key="id" :props="addEditProps"></el-tree></el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitAdd"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisible" @close="editFormClose" width="50%">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
          <el-form-item label="" prop="name"><el-input v-model="editForm.name"></el-input></el-form-item>
          <el-form-item label="" prop="instruction"><el-input type="textarea" v-model="editForm.instruction"></el-input></el-form-item>
          <el-form-item label="" prop="children">
            <el-tree :data="editForm.children" ref="editTree" show-checkbox node-key="id" :props="addEditProps"></el-tree>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitEdit"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import _ from 'lodash';
export default {
  data() {
    return {
      // 
      editDialogVisible: false,
      // 
      addDialogVisible: false,
      //
      queryInfo: {
        //
        keywords: '',
        //
        current_page: 1,
        //
        per_page: 25,
        // 
        total: 0
      },
      // 
      groupListData: [],
      // ID
      deleteId: '',
      // 
      addEditProps: {
        children: 'children',
        label: 'name'
      },
      // 
      editForm: {},
      // 
      editFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        rules: [{ required: true, message: '', trigger: 'change' }]
      },
      // 
      addForm: {
        name: '',
        instruction: '',
        status: '',
        rules: ''
      },
      // 
      addFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        status: [{ required: true, message: '', trigger: 'change' }],
        rules: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  created() {
    this.groupList();
    // 
    var self = this;
    document.onkeydown = function(e) {
      var key = window.event.keyCode;
      if (key === 13) {
        self.groupList();
      }
    };
  },
  methods: {
    /**
     * 
     */
    async groupList() {
      const { data: res } = await this.$http.get('group/list', { params: this.queryInfo });
      if (res.code !== 200) return this.$message.error(res.msg);
      this.queryInfo.total = res.data.total;
      res.data.data.forEach(item => {
        item.user = item.user.join('');
        if (item.user === '') {
          item.user = '';
        }
      });
      this.groupListData = res.data.data;
    },

    /**
     * 
     */
    handleSelectionChange(val) {
      // 
      var idArray = [];
      val.forEach(item => {
        idArray.push(item.id);
      });
      // 
      this.deleteId = idArray.join(',');
    },

    /**
     * 
     */
    deleteClick(id = '') {
      if (id !== '') {
        this.deleteId = id;
      }
      if (this.deleteId === '' || this.deleteId === undefined || this.deleteId == null) {
        return this.$message.warning('');
      }
      // 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          // 
          const { data: res } = await this.$http.delete('group/delete', { params: { id: this.deleteId } });
          if (res.code !== 200) return this.$message.error(res.msg);
          this.$message.success(res.msg);
          // 
          this.groupList();
        })
        .catch(() => {
          this.$message.info('');
          // 
          this.groupList();
        });
    },

    /**
     * 
     * @param {Object} val
     */
    handleSizeChange(val) {
      this.queryInfo.per_page = val;
      // 
      this.groupList();
    },

    /**
     * 
     * @param {Object} val
     */
    handleCurrentChange(val) {
      this.queryInfo.current_page = val;
      // 
      this.groupList();
    },

    /**
     * 
     * @param {Object} row 
     */
    statusEdit(row) {
      // 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          let status = '1';
          if (row.status === '1') {
            status = '0';
          }
          // 
          const { data: res } = await this.$http.put('group/statusedit', { id: row.id, status: status });
          if (res.code !== 200) return this.$message.error(res.msg);
          this.$message.success(res.msg);
          // 
          this.groupList();
        })
        .catch(() => {
          this.$message.info('');
          // 
          this.groupList();
        });
    },

    /**
     * 
     * @param {Object} id
     */
    async editDialog(id) {
      // ID
      const { data: res } = await this.$http.get(`group/query/id/${id}`);
      if (res.code !== 200) return this.$message.error(res.msg);
      this.editForm = res.data;
      // 
      setTimeout(() => {
        this.$refs.editTree.setCheckedKeys(this.editForm.rules);
      }, 500);
      // 
      this.editDialogVisible = true;
    },

    /**
     * 
     */
    submitEdit() {
      this.$refs.editFormRef.validate(async valid => {
        // 
        if (!valid) return;
        // 
        const form = _.cloneDeep(this.editForm);
        form.rules = this.$refs.editTree.getCheckedKeys().join(',');
        // 
        const { data: res } = await this.$http.put('group/edit', form);
        if (res.code !== 200) return this.$message.error(res.msg);
        this.$message.success(res.msg);
        // 
        this.editDialogVisible = false;
        // 
        this.groupList();
      });
    },

    /**
     * 
     */
    editFormClose() {
      // 
      this.$refs.editFormRef.resetFields();
    },

    /**
     * 
     */
    addFormClose() {
      // 
      this.$refs.addFormRef.resetFields();
    },

    /**
     * 
     */
    async addDialog() {
      const { data: res } = await this.$http.get('group/query');
      if (res.code !== 200) return this.$message.error(res.msg);
      this.addForm = res.data;
      // 
      this.addDialogVisible = true;
    },

    /**
     * 
     */
    submitAdd() {
      // 
      this.addForm.rules = this.$refs.addTree.getCheckedKeys();
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        this.addForm.rules = this.$refs.addTree.getCheckedKeys().join(',');
        const { data: res } = await this.$http.post('group/add', this.addForm);
        if (res.code !== 201) return this.$message.error(res.msg);
        this.$message.success(res.msg);
        // 
        this.addDialogVisible = false;
        // 
        this.groupList();
      });
    }
  }
};
</script>

<style lang="less" scoped></style>
